.project-heading {
  text-align: center;
  padding: 4rem 0 2rem 0;
}

.project-container {
  max-width: 1140px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 40px;
  padding-bottom: 5rem;
}

.project-card {
  background: #1a1919;
  padding: 1.2rem 1rem;
}

.project-card img {
  width: 100%;
}
.logo-link2 {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.logo-link2 > p {
  border-bottom: 1px solid;
}
.logo-link a :hover {
  color: teal;
}

.techStack {
  margin-top: 10px;
}

.project-title {
  color: white;
  padding: 1rem 0;
}

.pro-details p {
  padding-bottom: 1rem;
  font-size: 1.1rem;
  text-align: justify;
}

.pro-btns {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.pro-btns h1:hover {
  -webkit-transform: translateY(-3px);
  transform: translateY(-3px);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.project-card {
  position: relative;
  width: 100%;
  height: 450px;
  display: flex;
  gap: 10px;
  margin-bottom: 80px;
  border: 10px solid teal;
  border-radius: 20px;
  overflow: hidden;
}
.imgContainer {
  overflow: hidden;
}
.imgContainer > img {
  display: block;
  transition: transform 0.4s;
}

.imgContainer > img:hover {
  position: absolute;
  width: 80%;
  transform: scale(1.3);
  transform-origin: 0% 0%;
}

@media screen and (max-width: 770px) {
  .project-container {
    max-width: 90%;
    margin: auto;

    grid-template-columns: 1fr;
  }
  .project-card {
    flex-direction: column;
    align-items: center;
    height: 70%;
  }
  .imgContainer {
    width: 100%;
    height: 400px;
  }
  .imgContainer > img {
    width: 100%;
  }
}
