.mask {
  width: 100%;
  height: 100vh;
  position: relative;
}

.img1 {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  object-fit: cover;
}

.profile {
  width: 30%;
  border-radius: 50%;
  border: 1px solid black;
}
.mask::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: black;
  opacity: 0.5;
}

.hero {
  height: 100%;
  width: 100%;
}

.hero .headLine {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  text-align: center;
  justify-content: center;
}

.hero .headLine h1 {
  font-size: 2rem;
  padding: 0.6 0 1.5rem;
  margin-bottom: 30px;
}

.hero .headLine p {
  font-size: 1.8rem;
  font-weight: 300;
}
.typical {
  color: white !important;
  /* // border:1px solid black; */
  font-weight: 700 !important;
  font-size: 30px !important;
}

.headLine .btn {
  margin: 1rem 0.2rem;
}

@media screen and(max-width:640px) {
  .hero .content h1 {
    font-size: 3rem;
  }

  .hero .headLine p {
    font-size: 1.4rem;
  }
}
